*::before,
*::after {
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
}

body {
  background-color: #1E1B2E;
  color:  #F9F9F9;
  font-family: "Lato", sans-serif;
}

/* Stylování scrollbaru pro celou stránku */
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #24233600;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: #A855F7;
  border-radius: 10px;
  width: 6rem;
}

::-webkit-scrollbar-thumb:hover {
  background: #A855F7;
}

/* Pro Firefox - celá stránka */
html {
  scrollbar-width: 6rem;
  scrollbar-color: #A855F7 #24233600;
}

.all {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
}

.outlet {
  flex: 1;
}

h2 {
  margin-bottom: 15px;
  font-size: 30px;
  color: #A855F7;
  font-family: "Lexend", sans-serif;
  text-transform: uppercase;
}
