section {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    margin: 10px 50px;
}
section img {
    border-radius: 25px;
}

section .perex {
    font-size: 20px;
    font-weight: 600;
}

section p {
    font-size: 18px;
    line-height: 1.4;
    text-align: justify;
    color: #f5f5f5;
}

section a {
    color: #A855F7;
    margin-top: 15px;
    transition: all 0.4s linear;
}

section a:hover {
    color: #f5f5f5;
}

section button {
    font-weight: 600;
    padding: 5px 10px;
    margin: 5px;
    font-size: 16px;
    color: #f5f5f5;
    border-radius: 25px;
    cursor: pointer;
    transition: all 0.6s linear;
}

section button:hover {
    transform: scale(1.04);
}