header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    background-color: #A855F7;
    padding-right: 10%;
    font-weight: 600;
}

nav {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: baseline;
    margin-top: 20px;
    margin-bottom: 20px;
}

nav a {
    color: #F9F9F9;
    margin-right: 10px;
    display: inline-block;
    margin-bottom: 20px;
    text-decoration: none;
    transition: all 0.6s linear;
}

nav a:hover {
    color: #55F7A8;
}

.active {
    color: #55F7A8;
    text-decoration: underline;
}

.logout-btn {
    max-width: 300px;
    border: none;
    font-weight: 600;
    padding: 5px 10px;
    margin: 5px;
    margin-top: 15px;
    font-size: 14px;
    color: #A855F7;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.6s linear;
    background-color: #F9F9F9;
    transition: all 0.6s linear;
}

.logout-btn:hover {
    background-color: white;
    color:  #46c789;
    transform: scale(1.04);
}

