* {
    margin: 0%;
    padding: 0;
    box-sizing: border-box;
}

.login {
    width: 80%;
}

 .login-form {
    width: 100%;
    display: flex;
    flex-direction: row !important;
    align-items: flex-end;
    justify-content: space-between;

}

.form-section {
    max-width: 100%;
    display: flex; 
    flex-direction: column;
    justify-content: center;   
}

.form-section h3 {
    text-align: left;
}

.form-section form {
    padding: 15px;
    border-radius: 5px;
    position: relative;
    left: 0%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

input, textarea {
    width:100%;
    margin: 5px;
    background-color: #2b3a4b;
    border-radius: 5px;
    border: 2px solid #A855F7;
    color: #A855F7;
    padding: 5px 10px;
    outline: none;
}

input::placeholder,
textarea::placeholder {
    color: #f5f5f5b6;
    text-wrap: auto;
}

input:focus,
textarea:focus {
    border: 2px solid #f5f5f5b6;
}

.form-section input[type="submit"] {
    position: relative;
    right: 0px;
    bottom: 0%;
    /* max-width: 300px; */
    font-weight: 600;
    padding: 5px 10px;
    margin: 5px;
    margin-top: 15px;
    font-size: 14px;
    color: #f5f5f5;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.6s linear;
    background-color: #A855F7;
    text-wrap: auto;
}

.form-section input[type="submit"]:hover {
    transform: scale(1.02);
    font-size: 16px;
    background-color: #55F7A8;
    border-color: #55F7A8;;
}

.form {
    width: 80%;
    border-radius: 5px;
    margin-top: 50px;
    display: flex;
    flex-direction: column;
}

/* Stylování scrollbaru pro Webkit/Blink prohlížeče (Chrome, Safari, Opera, Edge) */
textarea::-webkit-scrollbar {
    width: 8px;
  }
  
  textarea::-webkit-scrollbar-track {
    background: #f5f5f53a;
    border-radius: 10px;
  }
  
  textarea::-webkit-scrollbar-thumb {
    background: #A855F7;
    border-radius: 10px;
  }
  
  textarea::-webkit-scrollbar-thumb:hover {
    background: #A855F7;
  }

.list:not(.is-form) {
    padding: 0px 15px;
    width: 80%;
    border-radius: 5px;
    margin-top: 50px;
    display: flex;
    flex-direction: column;
}

.item-admin {
    background-color: #2b3a4b;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    border-radius: 5px;
    margin: 5px;
    padding: 10px;
    border: #f5f5f53d 1px solid;
}

.item-admin:hover {
    box-shadow: #f5f5f5 0px 0px 5px;
}

.article-name {
    min-width: 200px;
}

.item-admin h4 {
    white-space: normal;
    overflow-wrap: anywhere;
    word-break: break-word; 
    /* color: blue; */
}

.item-admin small {
    overflow-wrap: anywhere;
    word-break: break-word;    
    white-space: normal;
    text-wrap: auto;
}

.form-section article:not(.visible, .archived) {
    border: 2px solid #A855F7;
    font-size: 1rem;
    padding: 10px 15px;
    width: 80%;
    border-radius: 5px;
    margin-top: 50px;
}

.form-section .visible {
    padding: 10px 15px;
    border: 2px solid rgb(14, 128, 22);
}

.form-section .archived {
    padding: 15px 10px;
    border: 2px solid red;
}

.remove {
    border: none;
}

.delete {
    background-color: red !important;
    border: none;
}

.restore, .edit {
    background-color: #f5f5f5 !important;
    font-weight: 700 !important;
    color: #A855F7 !important;
    border: none;
}

.cancel-edit {
    position: relative;
    right: 0px;
    bottom: 0%;
    max-width: 300px;
    font-weight: 600;
    padding: 5px 10px;
    margin: 5px;
    margin-top: 15px;
    font-size: 14px;
    color: #f5f5f5;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.6s linear;
    background-color: red !important;
    border: none;
}

.remove:hover,
.restore:hover,
.edit:hover,
.delete:hover,
.cancel-edit:hover {
    background-color: #55F7A8 !important;
    color: #f5f5f5;
    border: none;
}

.item-admin {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

}

.item-admin button {
    position: relative;
    right: 0px;
    bottom: 0%;
    max-width: 300px;
    font-weight: 600;
    padding: 5px 10px;
    margin: 5px;
    margin-top: 15px;
    font-size: 14px;
    color: #f5f5f5;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.6s linear;
    background-color: #A855F7;
}

.form-section button:hover:not(.list button) {
    transform: scale(1.04);
    background-color: #55F7A8;
    border-color: #55F7A8;;
}

/***Upload**/
/* Přidejte tyto styly do vašeho Form.css souboru */

.upload-progress {
    margin: 10px 0;
    height: 20px;
    width: 100%;
    background-color: #f0f0f0;
    border-radius: 10px;
    overflow: hidden;
    position: relative;
  }
  
  .progress-bar {
    height: 100%;
    background-color: #6c5ce7;
    transition: width 0.3s ease;
  }
  
  .upload-progress span {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    color: #333;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    font-weight: bold;
  }
  
  .error-message {
    color: #e74c3c;
    background-color: #ffeaea;
    padding: 10px;
    border-radius: 5px;
    margin-bottom: 15px;
    border-left: 4px solid #e74c3c;
  }
  
  .status-message {
    display: inline-block;
    margin-left: 10px;
    color: #6c5ce7;
    font-weight: bold;
  }
  
  .current-image {
    margin: 10px 0;
    padding: 8px;
    background-color: #f5f5f5;
    border-radius: 5px;
    font-size: 14px;
  }
  
@media (max-width: 599px) {
    .login-form {
        flex-wrap: wrap;
    }
}