.my-blog {
    padding-top: 20px;
    padding-bottom: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.all-articles {
    margin: 10px;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
    gap: 30px;
}

.one-article {
    background-color: #2b3a4b;
    padding: 20px;
    border-radius: 25px;
    display: flex;
    max-width: 400px;
    /* height: 80vh; */
    flex-direction: column;
    align-items: center;
    justify-content: center;
    line-height: 1.4;
    transition: all 0.6s linear;
}

.one-article:hover {
    transform: scale(1.01);
    box-shadow: 0px 0px 10px #f5f5f5b5;
}

.one-article a {
    text-decoration: none;
    color: #55F7A8;
}


.one-article img {
    border-radius: 25px;
    box-shadow: 0px 0px 10px #1E1B2E;
    border: #A855F7 1px solid;
}

.one-article button {
    font-weight: 600;
    padding: 5px 10px;
    margin: 5px;
    font-size: 16px;
    color: #f5f5f5;
    border-radius: 25px;
    cursor: pointer;
    transition: all 0.6s linear;
}

.one-article p {
    font-size: 16px;
    text-align: justify;
}

.one-article button:hover {
    transform: scale(1.04);
}

.read {
    background-color: #A855F7;
    border: 3px solid #A855F7;
}

.delete {
    background-color: transparent;
    border: 3px solid #A855F7;
    color: #55F7A8;
}

.read:hover {
    border: 3px solid #55F7A8;
    background-color: #55F7A8;
}

.delete:hover {
    background-color: #55F7A8;
    border: 3px solid #55F7A8;
}